<template>
  <div>
    <p>Profit Calc</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <table border="1">
      <tr>
        <th>exchange rate</th>
        <th>buy unit</th>
        <th>sale unit</th>
        <th>unit_skip</th>
        <th>quantity</th>
      </tr>
      <tr>
        <td><input v-model="exchange_rate"></td>
        <td><input v-model="buy_unit"></td>
        <td><input v-model="sale_unit"></td>
        <td><input v-model="unit_skip"></td>
        <td><input v-model="quantity"></td>
      </tr>
    </table>
    <hr>
    <table border="1">
      <tr>
        <th>profit rate</th>
        <th>profit amount</th>
        <th>in amount</th>
        <th>out amount</th>
        <th>sum amount</th>
      </tr>
      <tr>
        <td>{{(isNaN(profit_rate) || profit_rate == 0) ? 0 : profit_rate}}%</td>
        <td>{{(isNaN(profit_amount) || profit_amount == 0) ? 0 : profit_amount}}</td>
        <td>{{(isNaN(in_amount) || in_amount == 0) ? 0 : in_amount}}</td>
        <td>{{(isNaN(out_amount) || out_amount == 0) ? 0 : out_amount}}</td>
        <td>{{isNaN(profit_amount || profit_amount == 0) ? 0 : (profit_amount * quantity).toFixed(2)}}</td>
      </tr>
    </table>
    <hr>
    <div id="appChartDiv" class="appChart">
      <canvas id="saleAndProfitChart"></canvas>
    </div>
  </div>
</template>

<script>
import { Chart, registerables } from 'chart.js'
Chart.register(...registerables)

const SERVICE_RATE = 0.96525
let chartInstance

export default {
  name: 'ProfitCalcView',
  data () {
    return {
      exchange_rate: 0,
      buy_unit: 0,
      sale_unit: 0,
      unit_skip: 0.1,
      quantity: 500,
      sale_unit_rang: [],
      profit_amount_rang: [],
      profit_rate_rang: []
    }
  },
  watch: {
    sale_unit () {
      this.update_chart()
    },
    unit_skip () {
      this.update_chart()
    },
    exchange_rate () {
      this.update_chart()
    },
    buy_unit () {
      this.update_chart()
    }
  },
  computed: {
    profit_rate () {
      return this.calc_rate(this.exchange_rate, this.buy_unit, this.sale_unit)
    },
    profit_amount () {
      return this.calc_amount(this.exchange_rate, this.buy_unit, this.sale_unit)
    },
    in_amount () {
      return (parseFloat(this.exchange_rate) * parseFloat(this.buy_unit) * parseFloat(this.quantity)).toFixed(2)
    },
    out_amount () {
      return (parseFloat(this.quantity) * parseFloat(this.sale_unit)).toFixed(2)
    }
  },
  methods: {
    update_chart () {
      this.sale_unit_rang = this.sale_rang()
      this.profit_amount_rang = this.profit_rang(this.calc_amount)
      this.profit_rate_rang = this.profit_rang(this.calc_rate)
      this.draw_chart()
    },
    calc_rate (exchangeRate, buyUnit, saleUnit) {
      return (((SERVICE_RATE * parseFloat(saleUnit)) / (parseFloat(buyUnit) * parseFloat(exchangeRate)) - 1).toFixed(4) * 100).toFixed(2)
    },
    calc_amount (exchangeRate, buyUnit, saleUnit) {
      return ((SERVICE_RATE * parseFloat(saleUnit)) - (parseFloat(buyUnit) * parseFloat(exchangeRate))).toFixed(2)
    },
    sale_rang () {
      const rang = []
      const saleUnit = parseFloat(this.sale_unit)
      rang.push(parseFloat(saleUnit.toFixed(2)))
      let tempSale = saleUnit
      for (let i = 0; i < 5; i++) {
        tempSale = tempSale - parseFloat(this.unit_skip)
        rang.push(parseFloat(tempSale.toFixed(2)))
      }
      tempSale = saleUnit
      for (let i = 0; i < 5; i++) {
        tempSale = tempSale + parseFloat(this.unit_skip)
        rang.push(parseFloat(tempSale.toFixed(2)))
      }
      rang.sort((a, b) => a - b)
      return rang
    },
    profit_rang (fun) {
      const rang = []
      this.sale_unit_rang.forEach(element => {
        rang.push(fun(this.exchange_rate, this.buy_unit, element))
      })
      return rang
    },
    draw_chart () {
      if (chartInstance !== undefined) {
        chartInstance.destroy()
      }
      chartInstance = new Chart(document.getElementById('saleAndProfitChart'), {
        data: {
          datasets: [{
            type: 'bar',
            label: 'profit amount',
            data: this.profit_amount_rang,
            borderColor: 'rgb(0, 0, 255)',
            pointHoverBorderColor: 'rgb(0, 0, 0)',
            pointHoverBackgroundColor: 'rgb(0, 0, 0)'
          }, {
            type: 'line',
            label: 'profit rate',
            data: this.profit_rate_rang,
            fill: false,
            borderCapStyle: 'round',
            borderColor: 'rgb(255, 0, 0)',
            pointHoverBorderColor: 'rgb(0, 0, 0)',
            pointHoverBackgroundColor: 'rgb(0, 0, 0)'
          }],
          labels: this.sale_unit_rang
        },
        options: {
          responsive: true,
          title: {
            display: true,
            text: 'sale - profit relation'
          }
        }
      })
    }
  }
}
</script>

<style>
.appChart {
  height: 800px;
  width: 800px;
  margin: auto;
}
</style>
